import React from 'react'

import './NavBar.css'

class NavBar extends React.Component {
render(){
    return (
/*----------------- მთავარი მენიუ ლოგოთი -----------------  */
      <div className="container">
          <nav className="row navbar navbar-expand-lg navbar-light mt-3 " id="navbar-example2">
              <div className="col-2">
              <a className="navbar-brand font-weight-bold" href="#">luxestate</a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                  <span className="navbar-toggler-icon"></span>
                </button>
                </div>


{/*----------------- მენიუს კომპონენტები -----------------  */}      
                <div className="col-7">
                <div className="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul className="navbar-nav mr-auto ml-5">
                                          <li className="nav-item ">
                                            <a className="nav-link" href="#About">About</a>
                                          </li>
                                          <li className="nav-item ">
                                            <a className="nav-link" href="#Appartments">Appartments</a>
                                          </li>
                                          <li className="nav-item ">
                                            <a className="nav-link" href="#HowItWorks">How It Works</a>
                                          </li>
                                          <li className="nav-item ">
                                            <a className="nav-link" href="#Agents">Agents</a>
                                          </li>
                                          <li className="nav-item ">
                                            <a className="nav-link" href="#ContactUs">Contact Us</a>
                                          </li>
                      </ul>
                      </div>
                </div>


{/*----------------- შესვლა და დაწყება -----------------  */}
              <div className="col-3">
              <div className="collapse navbar-collapse" id="navbarSupportedContent">
             
                      <form className="form-inline my-2 my-lg-0 ">
                        <ul className="navbar-nav mr-auto">
                          <li className="nav-item ">
                            <a className="nav-link" href="#">Join Us</a>
                          </li>
                        </ul>
                        <button className="btn btn-warning my-2 my-sm-0 navbar-button" type="submit">Getting Started</button>
                      </form>
                </div>
              </div>
          </nav>
        
    </div>

      
    )
}
}

export default NavBar